.header {
  flex-grow: 1;
  padding: 0.875rem;
  border-bottom: 1px solid #f4f4f4; }
  .header .header__button:hover {
    background-color: transparent;
    color: #49aaca; }
  .header .header__button {
    color: #6a6767; }
  .header .header__help-button:hover {
    color: #ff5050; }

.header__logo {
  background-image: url(https://s3.amazonaws.com/development-silvercar-static-assets/assets/web-assets/silvercar-by-audi.svg);
  background-repeat: no-repeat;
  background-size: contain;
  flex-grow: 1;
  height: 1.5625rem;
  margin: 0;
  padding: 0;
  width: 9.375rem; }

.header__buttons {
  border: 1px solid #6a6767;
  border-radius: 6.25rem;
  margin: 0 1rem;
  padding: 0; }

.header__button:first-child {
  border-radius: 0rem;
  border-right: 1px solid #000; }

/*---------------- @media -------------------*/
@media screen and (max-width: 527px) {
  .header__buttons,
  .header .header__help-button {
    display: none; }
  .header {
    padding: 0; }
  .header__logo {
    height: 1rem; } }

.booking-footer {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(296px, 1fr));
  align-self: stretch;
  padding: 0.875rem; }

.booking-footer__inputs,
.booking-footer__texts,
.booking-footer__buttons {
  display: flex;
  align-items: center;
  justify-content: center; }

.booking-footer__text {
  align-items: center;
  display: flex;
  justify-content: center;
  font-weight: 400;
  margin: 8px; }

/*---------------- @media -------------------*/
@media screen and (max-width: 919px) {
  .booking-footer__inputs {
    display: none; } }

@media screen and (max-width: 619px) {
  .booking-footer {
    padding: 0.5rem; }
  .booking-footer__buttons {
    display: none; }
  .booking-footer__texts {
    justify-content: space-between; }
  .booking-footer__text {
    font-size: 0.875rem; } }

.bookingFooterNav {
  width: 100%; }

.location__card {
  border-left: 0.125rem solid #49aaca;
  display: flex;
  flex-direction: column;
  margin: 0.75rem 0;
  padding: 0.875rem;
  text-align: left;
  width: 17.1875rem; }
  .location__card:hover {
    background: #49aaca;
    cursor: pointer; }
  .location__card:focus {
    box-shadow: inset 0 0 0 2px #49aaca;
    outline: none; }

.location__card:hover .card__city,
.location__card:hover .card__icon-description {
  color: #fff; }

.location__card:hover .card__icon-icon {
  color: #fff;
  -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
  transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms; }

.card__city {
  font-size: 1.25rem;
  color: #454545;
  font-family: "Roboto", sans-serif; }

.card__icon {
  align-items: center;
  display: flex;
  flex-direction: row; }

.card__icon-icon {
  color: #a3a3a3;
  padding-right: 0.25rem; }

.card__icon-description {
  font-size: 0.75rem;
  color: #a3a3a3;
  font-family: "Roboto", sans-serif;
  overflow: hidden;
  padding: 0;
  text-overflow: ellipsis;
  white-space: nowrap; }

.search {
  align-self: stretch;
  padding: 0.5rem;
  background-color: #f4f4f4;
  text-align: center; }

.locations__selection {
  display: flex;
  flex-direction: column;
  align-items: center; }

.locations__title {
  font-size: 1.75rem;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  margin: 0.9375rem 0; }

.locations {
  overflow-y: scroll;
  width: 75%;
  height: calc(100vh - 190px); }

.locations__list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(275px, 1fr)); }

@supports not (display: grid) {
  .locations {
    display: flex;
    justify-content: center;
    padding: 0 1.5rem; }
  .locations__list {
    display: flex;
    flex-wrap: wrap;
    flex-basis: 915px; } }

/*---------------- @media -------------------*/
@media screen and (max-width: 527px) {
  .locations__title {
    font-size: 1rem; } }

body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.App {
  height: 100vh;
  display: flex;
  flex-direction: column; }

